.wrapper {
  background: rgb(225, 80, 230);
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  padding-bottom: 50px;
}
nav.navbar{
    background-color: blueviolet;
    border: 2px;
    border-color: black;
}
.navbar-brand{
    font-size: 2rem;
    margin-left: 2rem;
    font-weight: bold;
}
.nav-item{
    font-size: 1.5rem;
    color: white;
}
.navbar-text{
    font-size: 1.5rem;
    color: white;
    font-weight: bold;
}
.container {
    text-align: center;
}
.jumbotron{
    margin-top: 2rem;
    margin-bottom: 0;
    background-color: rgb(225, 80, 230);
}

.img-thumbnail {
    border: 4px rgb(99, 99, 99) solid;
    border-radius: 2px;
    width: 170px;
    height: 170px;
    margin: 10px 2px;
    position: relative;
    box-shadow: 4px 4px 10px rgb(56, 56, 56);
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
.col{
  margin-top: 2rem;
  margin-left: 5rem;
  margin-right: 5rem;
}
.img-thumbnail > .img-container {
    height: 100%;
    overflow: hidden;
    background:rgb(255, 255, 255);
  }

.img-thumbnail > .img-container > img {
    height: 100%;
  }

.img-thumbnail:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
.img-thumbnail:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
.footer{
    background-color: blueviolet;
    height: 3rem;
}
.container-fluid{
    text-align: center;
    padding: 1rem;
}
.text{
    margin-left: 3rem;
}
